<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process142"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span></p>
<p class="rvps2"><span class="rvts63"><br></span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts21">地点：复仇女士号任意位置</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务目标：</span>
<br><span class="rvts21">探索战船，召集同伴</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1082.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在该章节刚开始，所有的伙伴将会暂时离开主角。因此，需要把他们再次召集起来。腥红王子位于战船上层；伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹，野兽以及费恩位于中层；塞碧莉和洛许位于船的下层。</span>
<br><span class="rvts21">请务必在战船行驶之前招募好所有的同伴。因为之后战船将遭受大锤达利斯的猛烈袭击，并且在完成了拜访回音大厅后，所有的同伴将在之后章节永久不可使用或更换。（因为他们死在了船上）</span>
<br><span class="rvts21">另外，在战船下层有一面镜子，可以改编角色的容貌以及天赋。该功能只能在这里使用，请慎重考虑天赋问题。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



